.badge {
background: #ffffff;
box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
border-radius: 8px 8px 8px 8px;
overflow: hidden;
height: 380px;
width: 100%;
padding: 0;
}

.badge_header {
padding: 0.5rem 0;
height: 80px;
background: #41444b;
display: flex;
justify-content: center;
}

.badge_section-name {
display: flex;
justify-content: center;
align-items: center;
padding: 1rem 0;
}

.badge_section-info {
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
padding: 0.5rem 0;
background: #ececec;
}

.badge_avatar {
border-radius: 50%;
margin-right: 1rem;
width: 120px;
height: 120px;
}

.badge_footer {
height: 54px;
display: flex;
justify-content: center;
align-items: center;
color: #98ca3f;
font-weight: bold;
font-style: italic;
}